function resolveComWixMediaEnterprisePublicMediaV1EnterpriseItemsServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'manage._base_domain_': [
      {
        srcPath: '/_api/enterprise-public-media-server',
        destPath: '',
      },
    ],
    'bo._base_domain_': [
      {
        srcPath: '/_api/enterprise-public-media-server',
        destPath: '',
      },
    ],
    'wixbo.ai': [
      {
        srcPath: '/_api/enterprise-public-media-server',
        destPath: '',
      },
    ],
    'wix-bo.com': [
      {
        srcPath: '/_api/enterprise-public-media-server',
        destPath: '',
      },
    ],
    'apps._base_domain_': [
      {
        srcPath: '/_api/enterprise-public-media-server',
        destPath: '',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

